<script type="text/ecmascript-6">
export default {
  name: 'CellRenderByFunction',

  render(h) {
    const params = {
      ...this.params,
      row: this.params.data,
      index: this.params.rowIndex,
      column: this.params.colDef
    };

    if(typeof this.params.renderComponent !== 'function') {
      console.warn('缺少renderComponent参数，无法正确渲染组件')
      return h('div', {
        domProps: {
          innerHTML: `<span></span>`
        }
      })
    }
    return this.params.renderComponent(h, params);
  },
}
</script>
